.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.square{
  width: 50px;
  height: 50px;
}
.withBackground:nth-child(odd) {
  background-color: black;
}

.withBackground:nth-child(even) {
  background-color: gray;
}

.withOddBackground:nth-child(even) {
  background-color: black;
}

.withOddBackground:nth-child(odd) {
  background-color: gray;
}

.chessHeader{
  margin: 0 50px;
  display: flex;
}
.chessBoard{
  margin: 0;
  display: flex;
  flex-direction: column-reverse;
}

.chessRow{
  margin: 0;
  padding: 0;
  display: flex;
}